import React from 'react'
import {Navbar, Button, Card, ListGroup} from 'react-bootstrap'
import {appName, bg, variant} from './constants'
import './style.css'

function HomePage() {
    return (
        <React.Fragment>
            <div className="mainContainer">
                <div class="demo-card-wide mdl-card mdl-shadow--2dp">
                    <div class="mdl-card__title">
                        <h2 class="mdl-card__title-text">Welcome to {appName}</h2>
                    </div>
                    <div class="mdl-card__supporting-text">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                        Mauris sagittis pellentesque lacus eleifend lacinia...
                    </div>
                    <div class="mdl-card__actions mdl-card--border">
                        <a class="mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect" href="../register">
                        Get Started
                        </a>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default HomePage



